import axios from 'axios';

const baseURL ='http://localhost:3001'
const axiosInstance = axios.create({
	baseURL: baseURL,
		headers: {
		'Content-Type': 'application/json',
		accept: 'application/json',
	}, 
	crossdomain: true ,
	

});


export function validateCoupon(amount, couponCode){
     console.log({amount,couponCode});
    return axiosInstance.post("/validateCoupon",{amount,couponCode},{
        headers:{
          "Content-Type": "application/json",
          accept: 'application/json',
        }, 
        data: { couponCode: couponCode },
        crossdomain: true ,
      })
}

export function createForm(form){
return axiosInstance.post("/coupon/create",form,{headers:{
  "Content-Type": "application/json",
  accept: 'application/json',
}, 
crossdomain: true ,
})


}

export function editForm(form){
  return axiosInstance.patch("/coupon/edit",form,{headers:{
    "Content-Type": "application/json",
    accept: 'application/json',
  }, 
  crossdomain: true ,
  })
}
  
  export function deleteForm(form){
    alert(form)
    return axiosInstance.post(`/delete/coupon/`, {"code":form}, {
    headers:{
      "Content-Type": "application/json",
      accept: 'application/json',
    }, 
    
    crossdomain: true ,
    },)

  }


  export function getCouponForm(){
    return axiosInstance.get('/coupons/get',{
      headers:{
        "Content-Type": "application/json",
        accept: 'application/json',
      }, 
      
      crossdomain: true ,
      })
  }
