import React,{useEffect, useState} from "react";
import { FormControl,InputLabel,Input,FormHelperText, Button, Card, CardContent, ListItem } from '@material-ui/core';
import { validateCoupon } from "../services/coupons.service";
import './cart.component.css';
export default function Cart(){

    
    const [couponCode, setCouponCode] = useState("");
    const [amount, setAmount] = useState(0);
    const [finalAmount, setFinalAmount] = useState(0);
    function enterAmount(event){
         setAmount(event.target.value);
    }

    function enterCouponCode(event){
        setCouponCode(event.target.value);
    }

    function getCouponDiscount(){
          validateCoupon(amount,couponCode).then((response)=>{
              console.log(response)
              setFinalAmount(response.data);
          })
    }

   


    return (<div class="wrapper">
  <FormControl>
  <InputLabel htmlFor="total_amount">Total Amount</InputLabel>
  <Input id="total_amount" aria-describedby="amount" value={amount} onChange={(e)=>{enterAmount(e)}} />
  <FormHelperText id="my-helper-text">Total amount in rupees</FormHelperText>
  </FormControl>
  <FormControl>
  <InputLabel htmlFor="coupon_code">Coupon code</InputLabel>
  <Input id="coupon_code" aria-describedby="coupon_code" value={couponCode} onChange={(e)=>{enterCouponCode(e)}}/>
  <FormHelperText id="coupon_code">Enter a valid coupon code</FormHelperText>
</FormControl><br/>
<Button style={{backgroundColor:"aqua"}} onClick={()=>{getCouponDiscount()}}> Validate</Button>
<Card>
    <CardContent>
        <ListItem>
            Cost entered: {amount}
        </ListItem>
        <ListItem>
            Amount after Coupon is applied: {finalAmount}
        </ListItem>
    </CardContent>
</Card>

</div>)

}