import React, { useState } from 'react';

import { Button, List, ListItem, Card, CardContent, CardActions } from '@material-ui/core';
import coupon from '../models/coupon.model';
import { Dialog,DialogActions, FormGroup, InputLabel, DialogTitle, DialogContent, TextField, Select} from '@material-ui/core';
import { createForm, editForm, deleteForm, getCouponForm } from '../services/coupons.service';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
export default function Admin(){

    function formatDate(date) {
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();
    
        if (month.length < 2) 
            month = '0' + month;
        if (day.length < 2) 
            day = '0' + day;
    
        return [year, month, day].join('-');
    }
    
    const notify = (Message) => toast(Message);
    const [coupons, setCoupons] = useState([]);
    const [createCoupon, setCreateCoupon] = useState(false);
    const [deleteCoupon, setDeleteCoupon] = useState(false);
    const [editCoupon, setEditCoupon] = useState(false);
    const [getCoupons, setGetCoupons] = useState(false);
    const [couponCodeToBeDeleted, setCouponCodeToBeDeleted] = useState(false);
    const [formAdd, setFormAdd] = useState(false); //to toggle between the properties of creation, editing and seeing and the coupons 
    let newCoupon = new coupon("",formatDate(new Date()), formatDate(new Date()),"Flat",0,0); //a new coupon 
    const [Coupon, setCoupon] = useState(newCoupon); //coupon format for adding,editing
   

   function getAllCoupons(){
        getCouponForm().then(async(docs,err)=>{
            if(err){
               await  console.log(err);
            }
            else if(docs.data.length>0){
                   await console.log(docs.data);
                    setFormAdd(true);
                    setCoupons([...docs.data])
            }
        }).catch((err)=>{

        })
   }

    function handleFormClose(){
        setCreateCoupon(false);
        setDeleteCoupon(false);
        setEditCoupon(false);
        setGetCoupons(false);
    }

    function deleteCouponCode(){
        if(couponCodeToBeDeleted.length>5){
           
            deleteForm(couponCodeToBeDeleted).then((data)=>{
                notify(`Deleted ${couponCodeToBeDeleted}`);
                setCoupons([...coupons.filter((coupon)=>coupon.code !==couponCodeToBeDeleted.code)]);
               
            }).catch((err)=>{
                notify(`The coupon couldn't be deleted`)
            })
        }

        handleFormClose();
    }

    function submitForm(){
        if(Coupon.code.length>5&&(Coupon.discountType==="Flat"||Coupon.discountType==="Relative")&&Coupon.discount>0&&Coupon.minAmount>0)
         {
           if((Coupon.discountType==="Relative"&&Coupon.discount<100)||(Coupon.discountType==="Flat"))
            {
                createForm(Coupon).then((output)=>{notify("Coupon created successfully!")})
                handleFormClose();
            }
           else{
            notify("The coupon couldn't be created - tip: Check your discount properties")
           }  
         }
         else{
             notify("The coupon couldn't be created - tip: Coupon code should be of more than 5 characters")
         }
    }

    function submitEditForm(){
        if(Coupon.code.length>5&&(Coupon.discountType==="Flat"||Coupon.discountType==="Relative")&&Coupon.discount>0&&Coupon.minAmount>0)
         {
           if((Coupon.discountType==="Relative"&&Coupon.discount<100)||(Coupon.discountType==="Flat"))
            {
                editForm(Coupon).then((output)=>{notify("Coupon edited successfully!")})
                handleFormClose();
            }
           else{
            notify("The coupon couldn't be edited - tip: Check your discount properties")
           }  
         }
         else{
            notify("The coupon couldn't be edited - tip: Coupon code should be of more than 5 characters")
         }
    }

    return(<div className="wrapper">
        <ToastContainer />
         <Dialog open={createCoupon} onClose={handleFormClose} aria-labelledby="form-dialog-title">
             <DialogTitle>Create a coupon</DialogTitle>
             <DialogContent>
                 <FormGroup>
             <TextField
                autoFocus
                margin="dense"
                id="name"
                type="string"
                fullWidth
                value = {Coupon.code}

                onChange={(e)=>{ let newCoupon = {...Coupon}; newCoupon.code=e.target.value;
                    setCoupon(newCoupon)}}
                label = "coupon code"
              />
              </FormGroup>
              <TextField
                autoFocus
                margin="dense"
                id="start date"
                type="date"
                fullWidth
                
                value = {Coupon.startDate}
                onChange={(e)=>{let newCoupon = {...Coupon}; newCoupon.startDate=e.target.value;
                    setCoupon(newCoupon)}}
                label = "Start date"
              />
               <TextField
                autoFocus
                margin="dense"
                id="end date"
                type="date"
                fullWidth
                
                value = {Coupon.endDate}
                onChange={(e)=>{let newCoupon ={...Coupon}; newCoupon.endDate=e.target.value;
                    setCoupon(newCoupon)}}
                label = "End date"
              />
              <br/><br/>
             
              <InputLabel >Discount type</InputLabel>
               <Select label="Discount type" margin="dense" type="string" id="discounttype" defaultValue={Coupon.discountType} onChange={(e)=>{let newCoupon = {...Coupon}; newCoupon.discountType=e.target.value;
                    setCoupon(newCoupon)}}>
                  <option  value="Flat"> Flat</option>
                  <option value="Relative">Relative</option>
               </Select>
                <br/>
                <br/>
                <TextField autoFocus type="number" value={Coupon.minAmount} onChange={(e)=>{let newCoupon = {...Coupon}; newCoupon.minAmount=e.target.value;
                    setCoupon(newCoupon)}} label="Minimum Amount"/>
               <TextField autoFocus type="number"  value={Coupon.discount} onChange={(e)=>{ let newCoupon = {...Coupon}; newCoupon.discount=e.target.value;
                    setCoupon(newCoupon);console.log(Coupon)}} label="Discount"/>
             </DialogContent>
             <DialogActions>
             <Button onClick={()=>{submitForm();}}>Create coupon!</Button>
             </DialogActions>
             </Dialog>
             <Dialog open={editCoupon} onClose={handleFormClose} aria-labelledby="form-dialog-title">
             <DialogTitle>Edit a coupon</DialogTitle>
             <DialogContent>
                 <FormGroup>
             <TextField
                autoFocus
                margin="dense"
                id="name"
                type="string"
                fullWidth
                value = {Coupon.code}

                onChange={(e)=>{ let newCoupon = {...Coupon}; newCoupon.code=e.target.value;
                    setCoupon(newCoupon)}}
                label = "coupon code"
              />
              </FormGroup>
              <TextField
                autoFocus
                margin="dense"
                id="start date"
                type="date"
                fullWidth
                
                value = {Coupon.startDate}
                onChange={(e)=>{let newCoupon = {...Coupon}; newCoupon.startDate=e.target.value;
                    setCoupon(newCoupon)}}
                label = "Start date"
              />
               <TextField
                autoFocus
                margin="dense"
                id="end date"
                type="date"
                fullWidth
                
                value = {Coupon.endDate}
                onChange={(e)=>{let newCoupon ={...Coupon}; newCoupon.endDate=e.target.value;
                    setCoupon(newCoupon)}}
                label = "End date"
              />
              <br/><br/>
             
              <InputLabel >Discount type</InputLabel>
               <Select label="Discount type" margin="dense" type="string" id="discounttype" defaultValue={Coupon.discountType} onChange={(e)=>{let newCoupon = {...Coupon}; newCoupon.discountType=e.target.value;
                    setCoupon(newCoupon)}}>
                  <option  value="Flat"> Flat</option>
                  <option value="Relative">Relative</option>
               </Select>
                <br/>
                <br/>
                <TextField autoFocus type="number" value={Coupon.minAmount} onChange={(e)=>{let newCoupon = {...Coupon}; newCoupon.minAmount=e.target.value;
                    setCoupon(newCoupon)}} label="Minimum Amount"/>
               <TextField autoFocus type="number"  value={Coupon.discount} onChange={(e)=>{ let newCoupon = {...Coupon}; newCoupon.discount=e.target.value;
                    setCoupon(newCoupon);console.log(Coupon)}} label="Discount"/>
             </DialogContent>
             <DialogActions>
             <Button onClick={()=>{submitEditForm()}}>Edit coupon!</Button>
             </DialogActions>
             </Dialog>
             
                 <List style={{display:(formAdd?"block":"none")}}>
                     {
                         coupons.map((coupon,i)=>{
                            return (
                                 <ListItem key={i}>
                                     <Card>
                                    <CardContent>
                                     Code: {coupon.code} <br/>
                                     Minimum Amount: {coupon.minAmount} <br/>
                                     Discount type : {coupon.discountType} <br/>
                                     Discount : {coupon.discount} <br/>
                                     startDate: {coupon.startDate.slice(0,10)} <br/>
                                     endDate: {coupon.endDate.slice(0,10)}  <br/>
                                     </CardContent>
                                     <CardActions>
                                         <Button onClick={()=>{setCouponCodeToBeDeleted(coupon.code);deleteCouponCode()}}>
                                             <DeleteIcon/>
                                             </Button>
                                             <Button onClick={()=>{setEditCoupon(true);setCoupon({code:coupon.code, startDate:coupon.startDate, endDate:coupon.endDate, minAmount:coupon.minAmount, discountType:coupon.discountType, discount:coupon.discount})}}>
                                             <EditIcon/>
                                             </Button>
                                     </CardActions>
                                     
                                     </Card>
                                     </ListItem>
                                    
                             )
                         })
                     }
                     <Button onClick={()=>{setFormAdd(false)}}>
                         Go Back
                     </Button>
                </List>
         
         <List id="properties" style={{display:(formAdd?"none":"block")}}>
             <ListItem id="create" className="prop">
                 <Button onClick={()=>{setCreateCoupon(true)}}> Create a coupon</Button>
             </ListItem>
             <ListItem id="edit" className="prop">
                 <Button onClick={()=>setEditCoupon(true)}> Edit a coupon</Button>
             </ListItem>
             <ListItem id="see" className="prop">
                 <Button onClick={()=>{getAllCoupons();setGetCoupons(true)}}> See all coupons</Button>
             </ListItem>
         </List>
         
    </div>);
}